import React, { useState } from 'react';
//import connect from '@vkontakte/vk-connect';
//import PropTypes from 'prop-types';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import {ModalRoot, ModalPage, ModalPageHeader, FormLayout, SelectMimicry, FormLayoutGroup, Radio, PanelHeaderBack} from '@vkontakte/vkui';



	const ModalGroupSelector = ({shedule, go, values, getVariable,setVariable, modal, updateModal, modalBack, closeModal}) => {
		const [directionValue, setDirectionValue] = useState(values ? getVariable('direction') : '');
		const [courseValue, setCourseValue] = useState(values ? getVariable('course') : '');
		const [groupValue, setGroupValue] = useState(values ? getVariable('group') : '');

		let directionList = [];
		let coursesList = [];
		let groupsList = [];

		function refresh() {
			updateDirections();
			updateCourses();
			updateGroups();
		}

		refresh();

		function updateDirections() {
			shedule.forEach((e) => {
				let value = e['group']['destination'];
				if(!directionList.includes(value))
					directionList.push(value);
			});
		}

		function updateCourses() {
			shedule.forEach((e) => {
				let group = e['group'];
				if(!coursesList.includes(group['course']) && group['destination'] === directionValue)
					coursesList.push(group['course']);
			});
		}

		function updateGroups() {
			shedule.forEach((e) => {
				let group = e['group'];
				let dest = group['destination'];
				let course = group['course'];
				if(!groupsList.includes(group['name']) && dest === directionValue && course === courseValue)
					groupsList.push(group['name']);
			});
		}

		function storeData() {
			setVariable('direction', directionValue);
			setVariable('course', courseValue);
			setVariable('group', groupValue);
			closeModal();
			go("dayShedule");
		}

		return(
			<ModalRoot activeModal={modal}>
			<ModalPage
				id="groupSelectorPage"
				header={
					<ModalPageHeader
						left={<PanelHeaderBack onClick={() => modalBack()}/>}>
						Давайте всё настроим :)
					</ModalPageHeader>
				}
				onClose={
					() => closeModal()
				}>
				<FormLayout>
					<SelectMimicry 
						top="Направление" 
						placeholder="Выбрать направление" 
						onClick={() => { updateModal('direction'); refresh(); }}>{directionValue}</SelectMimicry>
					<SelectMimicry 
						top="Кyрс" 
						placeholder="Выбрать курс" 
						disabled={directionValue === ''} 
						onClick={() => { if(directionValue !== '') updateModal('course'); refresh(); }}>{courseValue}</SelectMimicry>
					<SelectMimicry 
						top="Группа" 
						placeholder="Выбрать группу" 
						disabled={courseValue === ''} 
						onClick={() => { if(courseValue !== '') updateModal('group'); refresh(); }}>{groupValue}</SelectMimicry>
					<Button
						disabled={groupValue === ''} 
						size="xl" 
						onClick={() => storeData() } 
						>Погнали</Button>
				</FormLayout>
			</ModalPage>

			<ModalPage
				id="direction"
				header={
					<ModalPageHeader
						left={<PanelHeaderBack onClick={() => modalBack()}/>}>
						Выберите направление
					</ModalPageHeader> //direction
				}
				onClose={
					() => modalBack()
				}
			>
				<FormLayoutGroup>
					{
						directionList.map(
							(title, id) => {
								return (
									<Radio key={id} name="direction" value={id} onClick={()=> {setDirectionValue(title); modalBack();}}>{title}</Radio>
								);
							}
						)
					}
				</FormLayoutGroup>
			</ModalPage>

			<ModalPage
				id="course"
				header={
					<ModalPageHeader
						left={<PanelHeaderBack onClick={() => modalBack()}/>}>
						Выберите курс
					</ModalPageHeader> //course
				}
				onClose={
					() => modalBack()
				}
			>
				<FormLayoutGroup>
					{
						coursesList.map(
							(title, id) => {
								return (
									<Radio key={id} name="direction" value={id} onClick={()=> { setCourseValue(title); modalBack();}}>{title}</Radio>
								);
							}
						)
					}
				</FormLayoutGroup>
			</ModalPage>

			<ModalPage
				id="group"
				header={
					<ModalPageHeader
						left={<PanelHeaderBack onClick={() => modalBack()}/>}>
						Выберите группу
					</ModalPageHeader> //group
				}
				onClose={
					() => modalBack()
				}
			>
				<FormLayoutGroup>
					{
						groupsList.map(
							(title, id) => {
								return (
									<Radio key={id} name="direction" value={id} onClick={()=> { setGroupValue(title); modalBack();}}>{title}</Radio>
								);
							}
						)
					}
				</FormLayoutGroup>
			</ModalPage>
		</ModalRoot>
	);
}
export default ModalGroupSelector;
